import {
	uniIcons
} from '../../../components/uni-icons/uni-icons.vue'
import {
	JSAPI,
	VOICEPROLOGUE,
	TEXTPROLOGUE
} from "../../../utils/api.js"
var jweixin = require('jweixin-module')
var jishiInterval;
import mpopup from "../../../components/xuan-popup/xuan-popup.vue"
var RECTIME
export default {
	components: {
		uniIcons,
		mpopup
	},
	data() {
		return {
			openInput: "",
			recStatus: "按住我开始录音",
			recComplete: false,
			isleft: true,
			isRight: false,
			progerssShow: false,
			recordTipShow: {
				opacity: "1"
			},
			isRec: false,
			isSave: false,
			isPlay: true,
			confirmShow: false,
			failShow: false,
			jsData: "",
			isDisable: true,
			localId: "",
			serverId: '',
			jishi: 0,
			xiaomiSucc: false,
			mobile: '',
			isJishi: false,
			startTime: '', //录音开始时间
			endTime: '', // 录音结束时间
			timeConsum: 0 //总耗时

		}
	},
	onLoad(options) {
		// 得到JSSDK
		var editData = JSON.parse(options.data)
		this.mobile = editData.mobile
		this.openInput = editData.text
		this.getJsAPI()
	},
	methods: {
		// 获得jssdk
		getJsAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("开场白得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['startRecord', 'stopRecord', 'playVoice', 'pauseVoice', 'stopVoice', 'uploadVoice',
							'onVoicePlayEnd'
						]
					}),
					jweixin.ready(function() {
						console.log("接口处理成功")
					}),
					jweixin.error(function() {
						console.log("接口处理失败")
					})
			}, err => {})
		},
		// 小秘说
		xiaomishuo() {
			this.isDisable = true
			console.log('自己录',this.isDisable)
			this.xiaomiSucc = false
			this.isleft = true,
			this.isRight = false
		},
		// 小秘取消
		cancelXiaomi() {
			this.xiaomiSucc = false
		},
		// 小米说保存
		saveXiaomi() {
			this.xiaomiSucc = true
		},
		// 小米说确认保存
		confirmXiaomi() {
			const params = {
				mobile: this.mobile,
				text: this.openInput
			}
			this.$request.url_request(TEXTPROLOGUE, params, 'GET', res => {
				uni.showToast({
					title:'保存成功！',
					icon:'success',
					mask:true,
					duration:1000
				})
				this.xiaomiSucc = false
				const detail = JSON.stringify({mobile:this.mobile})
				setTimeout(() => {
					uni.navigateBack({
						// url:'../ContactDetail/ContactDetail?detail='+detail
					})
				},1000)	
			}, err => {
				console.log("文本保存失败")
			})

		},
		// 小米说删除
		delXiaomi() {},
		// 自己录
		gotoSelfRecord() {
			console.log('自己录',this.isDisable)
			this.isDisable = false
			this.btnShow = false,
			this.isleft = false,
			this.isRight = true
		},
		// 开始录音
		startRec() {
			var that = this
			this.isJishi = true
			this.jishi = 0
			this.startTime = new Date().getTime()
			this.progerssShow = true,
				this.recordTipShow.opacity = "0",
				this.isRec = true,
				this.recStatus = "放开我停止录音",
				this.isPlay = true,
				console.log("开始录音")
			// 开始录音
			jweixin.startRecord()
			// 录音计时
			jishiInterval = setInterval(() =>{
				that.jishi++
			},1000)

		},
		// 停止录音
		stopRec() {
			clearInterval(jishiInterval)
			this.isJishi = false
			this.endTime = new Date().getTime()
			this.timeConsum = this.endTime - this.startTime
			console.log(this.timeConsum)
			var that = this
			if (this.timeConsum / 1000 < 3) {
				uni.showModal({
					content: '录音不足3秒',
					showCancel: false
				});
			} else {
				var _this = this
				this.isSave = true,
					this.recComplete = true
				jweixin.stopRecord({
					success: function(res) {
						_this.localId = res.localId;
						uni.showToast({
							title:'录音完成',
							icon:'none',
							mask:true,
							duration:1000
						})
					}
				});
			}

			this.progerssShow = false,
				this.recordTipShow.opacity = "1",
				this.isRec = false,
				this.recStatus = "按住我开始录音",
				this.isPlay = false
		},
		// 录音试听
		playRecord() {
			var _this = this
			console.log("试听的localId", this.localId)
			if (this.localId == '') {
				uni.showModal({
					content: '请先录制一段声音！',
					showCancel: false
				});
			} else {
				jweixin.playVoice({
					localId: _this.localId,
				})
			}

		},

		// 删除录音
		delRec() {
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				this.isJishi = false
				this.confirmShow = true
			}
		},
		// 保存录音
		confirmSave() {
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				this.isJishi = false
				this.failShow = true
			}

		},
		// 上传语音
		saveRecord() {
			var _this = this
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				console.log('得到的录音时间',this.timeConsum)
				jweixin.uploadVoice({
				  localId:_this.localId, // 需要上传的音频的本地ID，由stopRecord接口获得
				  isShowProgressTips: 1, // 默认为1，显示进度提示
				  success: function (res) {
				    _this.serverId = res.serverId; // 返回音频的服务器端ID
					const params = {
						mobile: _this.mobile,
						serverId: _this.serverId,
						voiceLength: _this.timeConsum
					}
					_this.$request.url_request(VOICEPROLOGUE,params,'GET',res =>{
						console.log("录音上传成功！")
						uni.showToast({
							title: '保存成功!',
							icon: 'success',
							duration: 1000
						})
						setTimeout(() =>{
							uni.navigateBack({
								
							})
						},1000)
						_this.confirmShow = false,
						_this.failShow = false
						_this.isJishi = false
					},err=>{
						
						uni.showToast({
							title: '保存失败!',
							icon: 'none',
							duration: 1000
						})
						console.log("录音上传失败！")
					})
				  }
				});
				this.confirmShow = false,
				this.failShow = false
			}
		},
		// 取消录音
		cancelSave() {
			this.isJishi = false
			this.confirmShow = false,
			this.failShow = false
		},
		confirmCancel() {
			this.isJishi = false
			this.confirmShow = false
		},
		// 返回上一页
		goback() {
			uni.navigateBack({
				delta: 1
			})
		}
	},
}
